// 引入通用table组件
import VXETable from '@/components/common/Table.vue';
// 所搜框
import SearchBox from '@/components/common/SearchBox';
// import { showSuccessMessage, showWarningMessage } from '@/utils/message';
import { showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import {
  STATUS_CODE,
  TITLE,
  BTN_TEXT,
  EQUIPMENT_MANAGEMENT_REPORT
} from '@/utils/constantsFile';
// import { CommonService } from '@/services/common.service';
// import { searchCondetionFunc } from '@/utils/publicFunction';
import {
  getDevice,
  listElectricRefrigerationReport,
  getElectricRefrigerationOpenAnd
} from '@/api/fbs.js';
// import { getGasTurbineRecordByTime } from '../../../../api/fbs';
export default {
  components: {
    // 加载通用table组件
    VXETable,
    SearchBox
  },

  data() {
    return {
      EQUIPMENT_MANAGEMENT_REPORT: EQUIPMENT_MANAGEMENT_REPORT,
      BTN_TEXT: BTN_TEXT,
      maxHeight: document.body.clientHeight - 250,
      deviceName: '',
      searchList: [],
      searchData: {
        conditionJson1: []
      },
      orderByData: {
        orderColumn: '',
        orderType: ''
      },
      typeOptions: [],
      groupOptions: [],
      typeValue: [],
      groupValue: [],
      bcOptions: [],
      bcId: '',
      dateTime: [],
      // 可编辑配
      editConfig: {
        trigger: 'click',
        mode: 'row',
        showStatus: false
        // icon: 'fa fa-file-text-o'
      },

      // 显示那个表
      showTable: true,
      // table是否加载
      tableLoadings: false,
      // 打印数据
      outData: [],
      // table数据
      tableDatas: [],
      // table表头配置
      tableColumns: [
        {
          field: 'deviceName',
          title: EQUIPMENT_MANAGEMENT_REPORT.DEVICE_NAME,
          // width: '130px',
          align: 'center'
          // sortable: true
          // fixed: 'left'
        },
        {
          field: 'totalElectricity',
          title: '总用电量',
          align: 'center',
          formatter({ cellValue, row, column }) {
            return row.totalElectricity.toFixed(2);
          }
        },
        {
          field: 'totalRefrigeratingCapacity',
          title: '总制冷量',
          align: 'center',
          formatter({ cellValue, row, column }) {
            return row.totalRefrigeratingCapacity.toFixed(2);
          }
        },
        {
          field: 'coldElectricRatio',
          title: '主机COP',
          align: 'center',
          formatter({ cellValue, row, column }) {
            return row.coldElectricRatio.toFixed(4);
          }
        },
        {
          title: BTN_TEXT.OPERATION,
          width: '100',
          align: 'center',
          fixed: 'right',
          slots: {
            default: ({ row }, h) => {
              var res = [];
              res = [
                <el-link
                  v-on:click={this.goInfo.bind(this, row)}
                  underline={false}
                  type="primary"
                >
                  详情
                </el-link>
              ];
              return res;
            }
          }
        }
      ],
      // table分页配置
      tablePages: {
        total: 0,
        currentPage: 1,
        pageSize: 20,
        align: 'right',
        pageSizes: [20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true
      },
      pickerOptions: {
        shortcuts: [
          {
            text: '最近一周',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: '最近一个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          },
          {
            text: '最近三个月',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }
        ],
        disabledDate: current => {
          return (
            current.getTime() >
            this.moment(
              `${this.moment(Date.now()).format('YYYY-MM-DD')} 23:59:59`
            ).format('x')
          );
        }
      },
      // 保存搜索内输入的搜索条件，防止翻页时变回无条件查询
      savedSearchCondition: [],
      canReviewCheck: false,
      // 模态框详情
      dialogVisible_time: false,
      tableData_time: [],
      tableColumn_time: [
        {
          field: 'deviceName',
          title: '设备名称',
          align: 'center'
        },
        // {
        //   field: 'bydydd',
        //   title: '10kv备用电源电度  KW·H',
        //   align: 'center'
        // },
        // {
        //   field: 'gzdydd',
        //   title: '10kv工作电源电度  KW·H',
        //   align: 'center'
        // },
        {
          field: 'powerConsumption',
          title: '用电量',
          align: 'center',
          formatter({ cellValue, row, column }) {
            return row.powerConsumption.toFixed(2);
          }
        },
        {
          field: 'refrigerationCapacity',
          title: '制冷量',
          align: 'center',
          formatter({ cellValue, row, column }) {
            return row.refrigerationCapacity.toFixed(2);
          }
        },
        {
          field: 'flow',
          title: '总流量',
          align: 'center',
          formatter({ cellValue, row, column }) {
            return row.flow.toFixed(2);
          }
        },
        {
          field: 'coldElectricRatio',
          title: '主机COP',
          align: 'center',
          // formatter({ cellValue, row, column }) {
          //   return row.coldElectricRatio.toFixed(4);
          // },
          slots: {
            default: ({ row }, h) => {
              var res = [];
              const value = parseFloat(row.coldElectricRatio.toFixed(4));
              res = [
                <span class={ this.isValueInRange(value) ? '' : 'redColor' }>{ value }</span>
              ];
              return res;
            }
          }
        },
        {
          field: 'startTime',
          title: '开始时间',
          align: 'center',
          formatter: 'formatDate',
          sortable: true
        },
        {
          field: 'endTime',
          title: '结束时间',
          align: 'center',
          formatter: 'formatDate'
        }
      ],
      tablePage_time: {
        total: 0,
        currentPage: 1,
        pageSize: 100,
        align: 'right',
        pageSizes: [10, 20, 50, 100, 200, 500],
        layouts: [
          'Sizes',
          'PrevJump',
          'PrevPage',
          'Number',
          'NextPage',
          'NextJump',
          'FullJump',
          'Total'
        ],
        perfect: true
      },
      tabs: [
        {
          name: '启停记录',
          index: 0
        },
        {
          name: '冷电比图表',
          index: 1
        }
      ],
      active: '启停记录',
      detail_dateTime: [],
      detailRow: {},
      selectDev: null,
      selectOptions: []
    };
  },
  watch: {},
  created() {
    // this.getDev();
    this.findList();
  },
  methods: {
    isValueInRange(value) {
      return value >= 3 && value <= 7;
    },
    toggle(tab) {
      this.active = tab.name;
      if (tab.name === '冷电比图表') {
        const data = this.tableData_time.filter(
          item => item.coldElectricRatio
        );
        const xAxisData = data.map(item => item.endTime);
        const seriesData = data.map(item => item.coldElectricRatio.toFixed(4));
        this.$nextTick(() => {
          this.showEchartsData(xAxisData, seriesData);
        });
      }
    },
    operateTime({ row }) {
      var timeValue = 0;
      if (row.endTime) {
        timeValue =
          (new Date(row.endTime).getTime() -
            new Date(row.startTime).getTime()) /
          1000;
      } else {
        timeValue =
          (new Date().getTime() - new Date(row.startTime).getTime()) / 1000;
      }
      var json = {
        cellValue: timeValue
      };
      return this.$options.filters.formatDifferentTime(json);
    },
    /**
     * @description: 刷新表格数据
     * @param 无
     * @return void
     * @author: cyb
     */
    refreshTableData(savedSearchCondition) {
      if (savedSearchCondition) {
        this.savedSearchCondition = [...savedSearchCondition];
      }
      this.findList();
    },
    /**
     * @description: 查询
     * @param obj ztree对象
     * @return: 无
     * @author: ZJM
     */
    search() {
      this.findList();
    },
    detail_search() {
      this.detailFindList();
    },
    /**
     * @description: 查询表格数据
     * @param {*}
     * @return {*}
     */
    async findList(flag) {
      // console.log(this.dateTime);
      var that = this;
      let endTime = `${this.moment(new Date()).format('YYYY-MM-DD')} 23:59:59`;
      let startTime = `${this.moment(new Date() - 60 * 3600 * 24 * 1000).format(
        'YYYY-MM-DD'
      )} 00:00:00`;
      this.detail_dateTime = [startTime, endTime];
      if (this.dateTime !== null) {
        if (this.dateTime.length !== 0) {
          startTime = this.dateTime[0];
          endTime = this.dateTime[1];
        } else {
          this.dateTime = [startTime, endTime];
        }
      } else {
        this.dateTime = [startTime, endTime];
      }

      var params = {
        size: this.tablePages.pageSize,
        current: this.tablePages.currentPage,
        startTime,
        endTime
      };

      // if (this.orderByData.orderColumn !== '') {
      //   params.orderColumn = this.orderByData.orderColumn;
      //   params.orderType = this.orderByData.orderType;
      // } else {
      //   params.orderColumn = 'startTime';
      //   params.orderType = 'desc';
      // }

      // if (this.selectDev) {
      //   params.deviceCode = this.selectDev;
      // }

      if (flag === 'export') {
        params.size = 99999999;
        params.current = 1;
      }

      try {
        this.tableLoadings = true;
        const res = await listElectricRefrigerationReport(params, {});
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          if (flag === 'export') {
            that.$refs.xGrid.exportDatax('制冷机设备性能分析', res.rows);
          } else {
            // 将请求数据写入table
            this.tableDatas = res.rows;
            // 将总条数写入table分页配置
            this.tablePages.total = res.total;
          }
        } else {
          // 提示异常信息
          this.tablePages.total = 0;
          this.tableDatas = [];
          showWarningMessage(res.message);
        }
      } catch (error) {
        this.tablePages.total = 0;
        this.tableDatas = [];
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        this.tableLoadings = false;
      }
    },
    showEchartsData(xAxisData, seriesData) {
      var myChart = this.$echarts.init(document.getElementById('gasEchart'));
      myChart.setOption(
        {
          title: {
            text: ''
          },
          tooltip: {
            trigger: 'axis',
            formatter: function(params) {
              return `${params[0].axisValue} <br/> ${params[0].marker}${params[0].seriesName}: ${params[0].data.toFixed(2)}`;
            }
          },
          toolbox: {
            // feature: {
            //   saveAsImage: {}
            // }
          },
          dataZoom: [
            {
              show: true,
              realtime: true,
              start: 0,
              end: 85
            },
            {
              type: 'inside',
              realtime: true,
              start: 0,
              end: 85
            }
          ],
          grid: {
            left: '4%',
            right: '6%',
            bottom: '8%',
            containLabel: true
          },
          xAxis: {
            type: 'category',
            name: '时间',
            boundaryGap: false,
            data: xAxisData
          },
          yAxis: {
            type: 'value',
            name: '冷量/总电量'
          },
          series: [
            {
              name: '冷电比',
              type: 'line',
              data: seriesData,
              label: {
                show: true
              }
            }
          ]
        },
        true
      );
    },
    /**
     * @description: 执行table分页
     * @param currentPage 第几页
     * @param pageSize 一页几条
     * @return: 无
     * @author: CH
     */
    onTablePageChanges(currentPage, pageSize) {
      // 点击页数写入table分页配置
      this.tablePages.currentPage = currentPage;
      // 一页条数夜路table分页配置
      this.tablePages.pageSize = pageSize;
      // 调用查询接口
      this.findList();
    },
    onDetailTablePageChanges(currentPage, pageSize) {
      console.log(currentPage, pageSize);
      this.tablePage_time.currentPage = currentPage;
      // 一页条数夜路table分页配置
      this.tablePage_time.pageSize = pageSize;
      // 调用查询接口
      this.detailFindList();
    },
    /**
     * @description: 表格排序
     * @param {*} property
     * @param {*} order
     * @return {*}
     */
    sortChange(property, order) {
      this.orderByData = {
        orderColumn: '',
        orderType: ''
      };
      this.orderByData = {
        orderColumn: property,
        orderType: order
      };
      this.onDetailTablePageChanges(1, this.tablePages.pageSize);
    },
    /**
     * @description:导出
     * @param {type}
     * @return:
     * @author: CH
     */
    /**
     * @description:导出
     * @param {type}
     * @return:
     * @author: CH
     */
    async exportTable() {
      this.findList('export');
    },
    // 模态框详情
    goInfo(row) {
      this.tablePage_time.total = 0;
      this.tablePage_time.currentPage = 1;
      this.tableData_time = [];

      this.detailRow = row;
      this.detailFindList();
      this.dialogVisible_time = true;
    },
    handleClose_time() {
      this.dialogVisible_time = false;
      // this.hackReset = false;
    },
    async detailFindList(flag) {
      var that = this;
      try {
        // this.tableLoadings = true;
        var params = {
          size: this.tablePage_time.pageSize,
          current: this.tablePage_time.currentPage,
          deviceCode: this.detailRow.deviceCode,
          startTime: this.detail_dateTime[0],
          endTime: this.detail_dateTime[1]
        };
        if (this.orderByData.orderColumn !== '') {
          params.orderColumn = this.orderByData.orderColumn;
          params.orderType = this.orderByData.orderType;
        } else {
          params.orderColumn = 'startTime';
          params.orderType = 'desc';
        }
        // if (this.detailRow.endTime) {
        //   params.endTime = this.detailRow.endTime;
        // } else {
        //   params.endTime = `${this.moment(new Date()).format(
        //     'YYYY-MM-DD HH:mm:ss'
        //   )}`;
        // }

        if (flag === 'export') {
          params.size = 99999999;
          params.current = 1;
        }

        const res = await getElectricRefrigerationOpenAnd(params, {});
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          if (flag === 'export') {
            that.$refs.detail.exportDatax(
              that.detailRow.deviceName + '设备启停记录',
              res.rows
            );
          } else {
            // 将请求数据写入table
            this.tableData_time = res.rows;
            // 将总条数写入table分页配置
            this.tablePage_time.total = res.total;
            if (this.active === '冷电比图表') {
              this.toggle({ name: '冷电比图表' });
            }
          }
        } else {
          // 提示异常信息
          this.tablePage_time.total = 0;
          this.tableData_time = [];
          showWarningMessage(res.message);
        }
      } catch (error) {
        this.tablePage_time.total = 0;
        this.tableData_time = [];
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        // this.tableLoadings = false;
      }
    },
    exportDetail() {
      this.detailFindList('export');
    },
    async getDev() {
      try {
        // this.tableLoadings = true;
        const res = await getDevice(
          {
            collectType: 'ElectricRefrigeration'
          },
          {}
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // 将请求数据写入table
          this.selectOptions = res.rows;
          this.selectOptions.unshift({
            deviceName: '全部',
            deviceCode: null
          });
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
        // this.tableLoadings = false;
      }
    }
  }
};
